import type { Meta, StoryFn } from '@storybook/web-components';
import { ButtonVariant, OverlayContentAlign } from '@tiger-analytics/ui';
import { html } from 'lit';

export default {
  title: 'Overlay/ta-overlay',
  component: 'ta-overlay',
  parameters: {
    controls: { expanded: true },
  },
  argTypes: {
    'id': {
      description: 'Mandatory - unique id for testing',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    'visible': {
      description: 'Controls the visibility of the overlay',
      table: {
        defaultValue: { summary: 'false' },
      },
      control: false,
    },
    'containerId': {
      description: 'Mandatory - id of the container to trap the focus within.',
      table: {
        defaultValue: { summary: '' },
      },
      control: false,
    },
    'overlayContentAlign': {
      options: [
        OverlayContentAlign.TopLeft,
        OverlayContentAlign.TopCenter,
        OverlayContentAlign.TopRight,
        OverlayContentAlign.MiddleLeft,
        OverlayContentAlign.MiddleCenter,
        OverlayContentAlign.MiddleRight,
        OverlayContentAlign.BottomLeft,
        OverlayContentAlign.BottomCenter,
        OverlayContentAlign.BottomRight,
      ],
      description: 'Alignment of the overlay content',
      table: {
        defaultValue: { summary: OverlayContentAlign.MiddleCenter },
      },
      control: { type: 'select' },
    },
    'on-close': {
      description: 'Event to perform task on overlay deactivation',
      table: {
        category: 'Custom Events',
      },
      control: false,
    },
  },
} as Meta;

const description = `The ta-overlay component is the base for creating overlay-type components like modals. It provides a content slot and focus management for accessibility. Before creating a new component with ta-overlay, please check the components list for any existing overlay components, such as modals, that may suit your needs.`;

const Template: StoryFn = (args) => {
  // Create a function to toggle the visibility of the overlay
  const toggleOverlayVisibility = () => {
    const overlay = document.querySelector('ta-overlay') as HTMLElement;
    const isVisible = overlay?.hasAttribute('visible');
    if (isVisible) {
      overlay.removeAttribute('visible');
    } else {
      overlay.setAttribute('visible', 'true');
    }
  };

  const onCloseHandler = () => {
    const buttonComponent = document.getElementById('activate-overlay');
    if (buttonComponent) {
      buttonComponent.focus();
    }
  };

  return html`
    <ta-grid-container .showSideMargin="${false}">
      <ta-grid-column style="gap: 10px">
        <ta-button
          id="activate-overlay"
          @click="${toggleOverlayVisibility}"
          variant="${ButtonVariant.Secondary}"
          >Show Overlay</ta-button
        >
        <ta-overlay
          id="test-overlay"
          containerId="children-content-wrapper"
          .visible="${args.visible}"
          .overlayContentAlign="${args.overlayContentAlign}"
          @onClose="${onCloseHandler}">
          <div
            id="children-content-wrapper"
            style="background: white; padding: 16px; border-radius: 8px; box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25);">
            <h2>Overlay Content</h2>
            <p>
              This is some content inside the overlay. Click the close button below to dismiss it.
            </p>
            <ta-button
              id="overlay-close"
              @click="${toggleOverlayVisibility}"
              variant="${ButtonVariant.Secondary}"
              >Close Overlay</ta-button
            >
            <ta-button id="overlay-dummy-button-1" @click="${() => window.alert('Dummy button 1')}"
              >Dummy button 1</ta-button
            >
            <ta-button
              id="overlay-dummy-button-2"
              @click="${() => window.alert('Dummy button 2')}"
              variant="${ButtonVariant.Destruct}"
              >Dummy button 2</ta-button
            >
          </div>
        </ta-overlay>
        <ta-button
          id="overlay-demo-button-1"
          @click="${() => window.alert('Demo button 1')}"
          variant="${ButtonVariant.Destruct}"
          >Demo button 1</ta-button
        >
      </ta-grid-column>
      <ta-grid-column>
        <div class="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
      </ta-grid-column>
      <ta-grid-column>
        <div class="story-description">${description}</div>
      </ta-grid-column>
    </ta-grid-container>
  `;
};

export const CodeAndAccessibility = Template.bind({});
CodeAndAccessibility.args = {
  visible: false,
  overlayContentAlign: OverlayContentAlign.MiddleCenter,
};
